@import '~node_modules/modern-normalize/modern-normalize.css';
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:opsz,wght@9..40,400;9..40,700&family=Inter:wght@600&family=Poppins:wght@400;500;600&display=swap');

@font-face {
  font-family: 'Sabir Mono';
  src: url('./../assets/fonts/SabirMonoRegular.otf') format('truetype');
}

@font-face {
  font-family: 'Venryn Sans';
  src: url('./../assets/fonts/venryn-sans.semibold.ttf') format('truetype');
}

body {
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  line-height: normal;
}

html,
body,
h2 {
  font-weight: 400;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

.hero-wrapper {
  height: 100%;
  position: relative;
  background: radial-gradient(
    85.37% 120.12% at 50.04% -11.47%,
    #014959 0%,
    #000c0f 100%
  );
}

button,
a {
  cursor: pointer;
}

canvas {
  position: absolute;
  bottom: 0;
  left: 0;
  opacity: 0.3;
}

.section-title {
  font-family: Venryn Sans;
  font-size: 22px;
  line-height: normal;
  margin-bottom: 32px;

  @include media-min(tablet) {
    font-size: 28px;
  }

  @include media-min(laptop) {
    font-size: 30px;
  }

  @include media-min(desktop) {
    font-size: 44px;
  }
  @include media-min(fullHD) {
    font-size: 64px;
  }
}
